import { Injectable } from '@angular/core';
import { Cards, Card } from './dati';
import { HttpClient } from '@angular/common/http';
import {AuthService} from './auth.service';

@Injectable({
  providedIn: 'root'
})
export class RecipesService {

  single_recipe: Recipe;
  singleric: Array<Cards> = [];

  /*maindeck: Array<Cards> = [];
  extradeck: Array<Cards> = [];
  sidedeck: Array<Cards> = [];*/


  allric: Array<Recipes> = [];
  all_comment: Array<Comments> = [];

  all_comment_ids: Array<allidcomment> = [];
  recipesid: Array<allidrecipe> = [];

  constructor(private http: HttpClient, public utente: AuthService) { }

  Addrecipes(name: string, desc: string): void{
    name = name.trim();
    desc = desc.trim();

    if (!name) { return; }
    if (!desc) { return; }
    let url = "http://localhost:8000/ricetta/user/"+localStorage.getItem("token_decoded_userid")
    let recipe = {}

    recipe["nomeric"] = name;
    recipe["desc"] = desc;

    this.http.post(url, recipe).subscribe((data: any) => {
        alert(data.status)
    });

  }

  Getrecipe(id: number): Array<Cards>{
    let url = "http://localhost:8000/ricetta/"+id
    this.http.get(url).subscribe((data: Array<Cards>) => {
      this.singleric = data;
    });
    return this.singleric;
  }

  /*organizzed_ARRAYS(){
    for(let card of this.singleric["cards"]){
      if(card.type == 'main'){
        this.maindeck.push(card);
      }else if(card.type == 'extra'){
        this.extradeck.push(card);
      }else if(card.type == 'side'){
        this.sidedeck.push(card);
      }
    }
    console.log(this.maindeck[0]);
    console.log(this.extradeck[0]);
    console.log(this.sidedeck[0]);
  }*/

  Getallallrecipes(): Array<Recipes>{
    let url = "http://localhost:8000/recipes"
    this.http.get(url).subscribe((data: Array<Recipes>) => {
      this.allric = data;
    });
    return this.allric;
  }

  Getrecipesidofuser(){
    let url = "http://localhost:8000/recipes/"+localStorage.getItem("token_decoded_userid")+"/user";
    this.http.get(url).subscribe((data: Array<allidrecipe>) => {
      this.recipesid = data;
    });
    //console.log(this.recipesid['recipes'][0].idric);
    //console.log(this.recipesid['recipes'].length);
  }

  ControllRecipe(id: number): number{
    for(let i = 0; i<=this.recipesid['recipes'].length; i++){
      if(this.recipesid['recipes'][i].idric == id){
        return id;
      }else{
        return null;
      }
    }
  }

  Addcomment(id: number, desc: string){
    let url = "http://localhost:8000/comments/user/"+localStorage.getItem("token_decoded_userid")+"/ricetta/"+id
    let comment = {};
    let list_id = {};
    comment["desc"] = desc;

    this.http.post(url, comment).subscribe((data: any) => {
      console.info(data);
      comment["id_com"] = data.id;
      comment["nome"] = this.utente.UserLoggedName;
      comment["cognome"] = this.utente.UserLoggedCognome;
      list_id["id_com"] = data.id;
    });
    this.all_comment_ids["comments"].push(list_id);
    this.all_comment["comments"].push(comment);

    alert("commento pubblicato!")
  }

  Getrecipescomments(id: number){
    let url = "http://localhost:8000/comments/recipe/"+id;
    this.http.get(url).subscribe((data: Array<Comments>) => {
      this.all_comment = data;
    });
  }

  Getrecipescommentsids(id: number, iduser: number){
    let url = "http://localhost:8000/comments/recipe/"+ id +"/user/" + iduser;
    this.http.get(url).subscribe((data: Array<allidcomment>) => {
      this.all_comment_ids = data;
    });
  }

  DeleteRecipe(idrecipe: number, index: number){
    this.allric["recipes"].splice(index, 1);
    let url = "http://localhost:8000/recipe/"+idrecipe;
    this.http.delete(url).subscribe((data: any) => {
      console.log(data);
    })
  }

  DeleteComment(idcomment: number, index: number){
    this.all_comment["comments"].splice(index, 1);
    let url = "http://localhost:8000/comment/"+idcomment;
    this.http.delete(url).subscribe((data: any) => {
      console.log(data);
    })
  }
}

export interface Recipe{
  idric: number;
  nomeric: string;
  desc: string;
}

export interface Recipes{
  recipes: Array<Recipe>;
}

export interface idrecipe{
  idric: number;
}

export interface idcomment{
  id_com: number;
}

export interface allidrecipe{
  recipes: Array<idrecipe>;
}

export interface allidcomment{
  comments: Array<idcomment>;
}

export interface Comment{
  nome: string;
  cognome: string;
  id_com: number;
  desc: string;
}

export interface Comments{
  comments: Array<Comment>;
}
